<template>
    <jb-form :model="formData" :rules="rules" ref="varNamespaceForm">
        <jb-form-item :label="''" required :property="'name'">
            <jb-input
                v-model="formData.name"
                :placeholder="' []'"
                :maxlength="30" />
        </jb-form-item>
        <jb-form-item
            :label="''"
            :desc="''"
            property="defaultValue">
            <bk-input v-model="formData.defaultValue" :placeholder="' []'" />
        </jb-form-item>
        <jb-form-item :label="''">
            <bk-input
                v-model="formData.description"
                :placeholder="' []'"
                type="textarea"
                maxlength="100" />
        </jb-form-item>
        <jb-form-item style="margin-bottom: 0;">
            <bk-checkbox v-model="formData.required" :true-value="1" :false-value="0">
                {{ '' }}
            </bk-checkbox>
        </jb-form-item>
    </jb-form>
</template>
<script>
       import {
        globalVariableNameRule,
    } from '@utils/validator';
    import JbInput from '@components/jb-input';

    export default {
        name: 'VarNamespace',
        components: {
            JbInput,
        },
        props: {
            variable: {
                type: Array,
                default () {
                    return [];
                },
            },
            data: {
                type: Object,
                default: () => ({}),
            },
        },
        data () {
            return {
                formData: { ...this.data },
            };
        },
        created () {
            this.rules = {
                name: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur',
                    },
                    {
                        validator: globalVariableNameRule.validator,
                        message: globalVariableNameRule.message,
                        trigger: 'blur',
                    },
                    {
                        validator: val => !this.variable.some(item => item.name === val),
                        message: '',
                        trigger: 'blur',
                    },
                ],
            };
        },
        methods: {
            submit () {
                return this.$refs.varNamespaceForm.validate()
                    .then(() => {
                        this.$emit('on-change', {
                            ...this.formData,
                            type: 2,
                        });
                    }, validator => Promise.reject(validator.content));
            },
        },
    };
</script>
