<template>
    <jb-form form-type="vertical">
        <jb-form-item
            :label="''"
            required
            :desc="nametips">
            <div class="variable-type-wraper">
                <bk-radio-group v-model="globalType">
                    <bk-radio-button
                        class="item"
                        value="string"
                        
                        :name="''"
                        v-bk-tooltips="''">
                        {{ '' }}
                    </bk-radio-button>
                    <bk-radio-button
                        class="item"
                        value="namespace"
                        
                        v-bk-tooltips="''">
                        {{ '' }}
                    </bk-radio-button>
                    <bk-radio-button
                        class="item"
                        value="host"
                        
                        v-bk-tooltips="''">
                        {{ '' }}
                    </bk-radio-button>
                    <bk-radio-button
                        class="item"
                        value="password"
                        
                        v-bk-tooltips="''">
                        {{ '' }}
                    </bk-radio-button>
                    <bk-radio-button
                        class="item"
                        value="array"
                        
                        v-bk-tooltips="'Array '">
                        {{ '' }}
                    </bk-radio-button>
                </bk-radio-group>
            </div>
        </jb-form-item>
        <component
            ref="handler"
            :is="globalVarCom"
            :data="formData"
            v-bind="$attrs"
            v-on="$listeners" />
    </jb-form>
</template>
<script>
       import VarString from './string';
    import VarNamespace from './namespace';
    import VarHost from './host';
    import VarPassword from './password';
    import VarArray from './array';
    import { createVariable } from '../util';

    export default {
        name: 'GlobalVar',
        components: {
            VarString,
            VarNamespace,
            VarHost,
            VarPassword,
            VarArray,
        },
        props: {
            data: {
                type: Object,
                default () {
                    return {};
                },
            },
        },
        data () {
            return {
                globalType: 'string',
                formData: {},
            };
        },
        computed: {
            globalVarCom () {
                const globalVarMap = {
                    string: VarString,
                    namespace: VarNamespace,
                    host: VarHost,
                    password: VarPassword,
                    array: VarArray,
                };
                if (!Object.prototype.hasOwnProperty.call(globalVarMap, this.globalType)) {
                    return 'div';
                }
                return globalVarMap[this.globalType];
            },
            isTypeDisabled () {
                return !!Object.keys(this.data).length;
            },
        },
        watch: {
            data: {
                handler (data) {
                    if (data.name) {
                        // 
                        this.formData = data;
                    } else {
                        // 
                        // 
                        this.formData = createVariable(null, '');
                    }
                    this.globalType = this.formData.typeDescription;
                },
                immediate: true,
            },
        },
        created () {
            this.nametips = {
                theme: 'dark',
                content: ' ${} ',
                width: 200,
            };
        },
        methods: {
            submit () {
                return this.$refs.handler.submit && this.$refs.handler.submit();
            },
            reset () {
                return this.$refs.handler.reset && this.$refs.handler.reset().then(() => {
                    this.globalType = '';
                });
            },
        },
    };
</script>
<style lang="postcss">
    .variable-type-wraper {
        .item {
            .bk-radio-button-text {
                width: 108px;
            }
        }
    }
</style>
